
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {httpUrl} from '../../http_url';


export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
show: true,
time: 3,
}
},
created(){

},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
Countdown
},
methods:{
goSign(){
let orderid = this.$route.query.orderid;
let step_type = this.$route.query.step_type;
if(step_type == '1'){
this.$router.push({
path:'/verify_step',
query: {
orderid:orderid
}
});
}else {
this.$router.push({
path:'/signature',
query: {
orderid:orderid
}
});
}
},
finish (index) {
this.show = false
this.goSign();
},

updatedata(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid:orderid,
is_sign:1
})
this.$axios({
method: 'post',
url:httpUrl.updatedata,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data);
this.$vux.loading.hide();
if(res.data.errCode==200){

}else if(res.data.errCode==201){
this.$vux.toast.show({
text: res.data.retMsg,
type:'warn',
onHide(){

}
});
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},

}
}



